.section__title--profile {
	opacity: .5;
	padding-bottom: 0;
	top: 85px;
	font-weight: 300;
	@include desktop {
		left: 10px;
	}
}

#navigation {
	top: 20px;
	&.fixed {
		top: 0;
	}
}

.breadcrumbs {
	list-style: none;
	display: flex;
	flex-wrap: wrap;
	margin-top: 70px;
	padding: 0;
	@include tablets {
		margin-top: 90px;
	}
}

.breadcrumb__link {
	display: block;
	padding: 10px 30px;
	border: 1px solid #fff;
	border-radius: 20px;
	margin-right: 20px;
	color: #fff;
	opacity: .7;
	font-size: 13px;
	@include tablets {
		margin-bottom: 10px;
	}
	&:hover {
		color: #fff;
		background-color: rgba(104, 173, 225, .5);
		border: 1px solid #66A7DB;
		opacity: 1;
	}
}

.breadcrumb__item--active {
	.breadcrumb__link {
		color: #fff;
		background-color: rgba(104, 173, 225, .5);
		border: 2px solid #66A7DB;
		opacity: 1;
	}
}

.bonus__wrapper {
	position: relative;
	width: 100%;
	height: 170px;
	background: linear-gradient(to left, #01AEEB 0%, #DA1845 100%);
	border-radius: 5px;
}

.bonus {
	position: absolute;
	border-radius: 5px;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 99%;
	height: 160px;
	background-color: #000;
	position: relative;
	color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
}

.bonus__text {
	display: flex;
	line-height: 1;
}

.bonus-red {
	background: linear-gradient(to right, #DA1845 0%, #AA396A 100%);
	background-clip: text;
	-webkit-text-fill-color: transparent;
	font-size: 17px;
}

.bonus__text-left {
	margin-right: 20px;
}

.bonus__text-right {
	font-size: 92px;
	@include middle {
		font-size: 70px;
	}
	@include phones {
		font-size: 50px;
	}
}

.bonus-big {
	display: block;
	color: #fff;
	background: none;
	font-size: 71px;
	text-transform: uppercase;
	font-weight: 500;
	@include middle {
		font-size: 51px;
	}
	@include phones {
		font-size: 32px;
	}
}

.balance {
	background-color: #fff;
	border-radius: 5px;
}

.balance__card {
	background-color: #fff;
	padding: 50px 30px;
	border-radius: 5px;
	margin-top: 25px;
	line-height: 1;
	@include middle {
		padding: 50px 10px;
	}
}

.balance__title {
	font-size: 25px;
	margin-bottom: 10px;
}

.balance__text {
	color: #D8D8D8;
	margin-bottom: 20px;
	font-size: 13px;
}

.balance__amount {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	margin-top: 20px;
	font-size: 71px;
	&--modal {
		.amount {
			margin: 0;
		}
		.currency {
			margin-right: 10px;
		}
	}
}

.balance__icon {
	align-self: flex-start;
}

.amount {
	font-size: 60px;
	margin: 0 20px;
	background: linear-gradient(to right, #01aeeb 0%, #01eca0 100%);
	background-clip: text;
	-webkit-text-fill-color: transparent;
	@include desktop {
		font-size: 40px;
	}
	@include tablets {
		font-size: 32px;
	}
	@include middle {
		font-size: 20px;
		overflow: hidden;
	}
	&--small {
		font-size: 37px;
		@include desktop {
			font-size: 2rem;
			margin: 0;
		}
		@include middle {
			font-size: 1.8rem;
		}
	}
	&--bonus {
		background: none;
		background-clip: unset;
		-webkit-text-fill-color: initial;
		color: #F845AD;
		margin-left: 40px;
		@include middle {
			margin-left: 0;
		}
	}
}

.amount__desc {
	font-size: 16px;
	@include middle {
		font-size: 13px;
	}
}

.amount__top {
	font-weight: 500;
}

.amount__bottom {
	color: #D8D8D8;
}

.currency {
	align-self: flex-end;
	margin-bottom: 10px;
	font-size: 25px;
	@include middle {
		font-size: 16px;
		margin-bottom: 5px;
	}
	&--small {
		margin-bottom: 10px;
		margin-left: 10px;
		font-size: 17px;
		@include desktop {
			margin-bottom: 5px;
		}
	}
}

.balance__link {
	margin: 20px 0;
	margin-left: 45px;
	font-size: 16px;
	font-weight: 500;
	line-height: 1;
	@include middle {
		font-size: 13px;
		margin-left: 0;
	}
	&--small {
		margin-left: 0;
		font-size: 13px;
	}
	&--modal {
		text-align: center;
		margin-left: 0;
		font-size: 13px;
	}
	&--title {
		margin-top: 45px;
	}
}

.balance__links {
	display: flex;
	justify-content: space-between;
	&--modal {
		.balance__code {
			padding: 10px 20px;
			width: 100%;
			margin-top: 20px;
			margin-left: 0;
		}
	}
}

.balance__code {
	padding: 0px 20px;
	background: linear-gradient(to right, #01aeeb 0%, #01eca0 100%);
	border-radius: 30px;
	width: 75%;
	margin-left: 40px;
	opacity: .8;
	display: flex;
	justify-content: center;
	align-items: center;
	@include middle {
		width: 80%;
		margin-left: 0;
	}
}

.balance__token {
	font-size: 24px;
	margin-top: 45px;
	@include desktop {
		font-size: 20px;
	}
	@include tablets {
		font-size: 16px;
	}
	@include phones {
		margin-top: 30px;
		margin-right: 30px;
		margin-left: -10px;
		font-size: 13px;
	}
}

.qr-icon {
	max-width: 100%;
	height: auto;
}

.code {
	margin-right: auto;
	font-size: 16px;
	@include tablets {
		font-size: 14px;
	}
	@include middle {
		font-size: 13px;
		overflow: auto;
	}
}

.btn--code {
	background-color: #fff;
	color: #000;
	padding: 10px 5px;
	max-width: 70px;
	text-transform: uppercase;
	font-size: 13px;
	@include tablets {
		display: none;
	}
}

.btn-qr {
	padding: 4px;
	max-width: 50px;
	margin-left: 10px;
	@include tablets {
		display: none;
	}
}

.qr-icon {
	max-width: 100%;
	height: auto;
}

.btn--ref {
	max-width: 150px;
	text-align: center;
	padding: 15px;
	@include tablets {
		max-width: 100px;
	}
	@include middle {
		font-size: 13px;
		padding: 10px;
		min-width: 70px;
	}
	@include phones {
		font-size: 12px;
		min-width: 60px;
	}
}

.btn--small {
	padding: 10px;
	max-width: 100px;
	margin-left: 10px;
}

.refill__modal {
	width: 40%;
	padding: 0;
	border-radius: 5px;
	display: none;
	margin-top: 100px;
	@include middle {
		width: 100%;
	}
}

.qr-code {
	text-align: center;
	margin-top: 20px;
}

.qr-code__pic {
	max-width: 100%;
	height: auto;
}

.balance__exchange {
	background-color: #E8E9ED;
	padding: 50px 30px;
	line-height: 1;
}

.exchange__title {
	font-size: 16px;
	margin-top: 20px;
}

.exchange__guide {
	font-size: 13px;
	margin-top: 20px;
}

.guide__item {
	padding-bottom: 10px;
}

.banner {
	background: url("../img/flag_usa.png") no-repeat 100% 0 /contain;
	background-color: #DA325B;
	padding: 50px 30px;
	border-radius: 5px;
	margin-top: 25px;
	line-height: 1;
	color: #fff;
	display: flex;
	flex-wrap: nowrap;
	@include middle {
		flex-direction: column;
		padding: 50px 15px;
	}
}

.banner__title {
	font-size: 25px;
	margin-bottom: 10px;
}

.banner__text {
	font-size: 13px;
}

.banner__left {
	flex: 0 0 60%;
	@include middle {
		flex: 0 0 100%;
	}
}

.banner__steps {
	display: flex;
	margin-top: 30px;
	justify-content: flex-start;
	align-items: center;
	@include middle {
		flex-direction: column;
		align-items: flex-start;
	}
}

.banner__step {
	display: flex;
	flex-direction: column;
	margin-right: 30px;
	@include tablets {
		margin-bottom: 20px;
	}
}

.step__desc {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	line-height: 1;
	margin-bottom: 20px;
}

.step__number {
	font-size: 25px;
	color: #001441;
	margin-right: 10px;
}

.btn--usa {
	background-color: #fff;
	color: #DA2752;
	font-size: 17px;
	text-transform: uppercase;
	max-width: 300px;
	padding: 15px 50px;
	@include middle {
		font-size: 14px;
	}
}

.total__card {
	background-color: #E8E9ED;
	border-bottom-right-radius: 5px;
	border-bottom-left-radius: 5px;
	padding: 40px 35px 40px 75px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	@include phones {
		padding: 10px;
	}
}

.total__title {
	font-size: 26px;
	@include middle {
		font-size: 20px;
	}
}

.total__number {
	font-size: 50px;
	@include middle {
		font-size: 30px;
	}
}

.total__text {
	color: #A3A8B4;
}