.verification__status {
	margin: 75px 0 275px 0;
	text-align: center;
}

.verification__title {
	color: #fff;
	font-size: 13px;
	opacity: .7;
}

.status {
	font-size: 71px;
	text-transform: uppercase;
	background: linear-gradient(to right, #01ECA0 0%, #01DDB2 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    opacity: .5;
    &--progress {
    	background: linear-gradient(to right, #DA1845 0%, #865285 100%);
    	background-clip: text;
       -webkit-text-fill-color: transparent;
        opacity: .8;
    }
}

.btn--verif {
	background-color: rgba(255, 255, 255, .3);
	width: 35%;
	margin: 0 auto;
}